@charset "utf-8";blockquote,body,fieldset,figure,h1,h2,h3,h4,h5,h6,hr,html,iframe,legend,li,ol,p,pre,textarea,ul{margin:0;padding:0}h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:400}ul{list-style:none}button,input,select{margin:0;outline:0}html{box-sizing:border-box}*,::after,::before{box-sizing:inherit}audio,canvas,embed,iframe,img,object,svg,video{height:auto;width:100%;vertical-align:top}iframe{border:0}table{border-collapse:collapse;border-spacing:0}td,th{padding:0}a{text-decoration:none;color:inherit}